@import url("app/less/Common/_var.less");
@import url("app/less/Common/_mixins.less");
@import url("app/less/Common/_fonts.less");


html {
  font-size: 24px;
  scroll-behavior: smooth;
}

html,
body {
  color: @font-color;
  width: 100%;
}


::selection {
  background: rgba(@main-color, 0.8);
  color: @white;
}

body {
  font-family: "Gilroy", sans-serif;
  font-size: 1rem;
  font-variant-ligatures: normal;
  font-variant-east-asian: normal;
  font-variant-caps: normal;
  font-variant-numeric: normal;
  font-stretch: 100%;
  font-style: normal;
  font-weight: 400;
  color: @font-color;
  letter-spacing: 0.2px;
  line-height: 1.25;
  margin: 0;
  padding: 0;
  // overflow-y: hidden;

}



*,
*:before,
*:after {
  box-sizing: border-box;
  // line-height: 1;
}


img,
picture,
source {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

h2 {
  overflow-wrap: break-word;
}

ul,
li,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  padding: 0;
  margin: 0;
  display: block;
  color: @font-color;
  cursor: pointer;
  font-weight: normal;
}

input,
select,
textarea {
  width: 100%;
  outline: none;
  border: none;
  text-align: center;
}


label {
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 300;
}

button {
  cursor: pointer;
  outline: none;
  background: unset;
  border: none;
}


main {
  width: 100%;
}


