.lobby__layer {
    .fPa((217%/2), (220%/2), (217px/2), (220px/2), 992px, 1600px);
}

.lobby__top {
    text-align: center;
}

.lobby__title {
    text-align: center;
}

.lobby__main-image {
    position: relative;

    &:before {
        content: '';
        display: block;
        padding-top: (532/1600) * 100%;
    }

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        z-index: 1;
        object-fit: cover;
    }
}

.lobby__images-wrap {
    flex-basis: ((1475 - ((87/2) + 715))/1475) * 100%;
    padding-right: (77px/2);
}

.lobby__info {
    flex-basis: (((87/2) + 571)/1475) * 100%;
    padding-left: (77px/2);
}


//carousel





@media screen and (max-width: 992px) {
    .lobby__info {
        width: 100%;
        flex-basis: 100%;
        padding-left: 0;
    }
}


